nav {
  width: 100%;
  font-size: 2rem;
  color: #fff;
  margin-bottom: 50px;
  background-color: saddlebrown;
  height: auto;
  font-family: 'Bungee', cursive;
}

a {
  color: #fff;
}

a:hover {
  color: red;
  text-decoration: none;
}

nav li {
  display: inline-block;
  padding: 20px 0;
  width: 33%;
  /* text-align: center; */
}

ul {
  /* padding-left: 0px; */
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 750px) {
  #score {
    font-size: 1rem;
  }
}

.footer {
  min-height: 100px;
  background: #E5E5E5;
  position: relative;
}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: #B8B8B8;
}

.wrapper {
  min-height: 100%;
  padding-bottom: 50px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  background-image: url(/React-Clicky-Game/static/media/field.611e213b.jpg);
}

.card {
    border-radius: 2px;
    width: 150px;
    height: 150px;
    margin: 1rem;
    position: relative;
    box-shadow: 4px 4px 10px rgb(56, 56, 56);
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    align-items: center;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    background:rgb(255, 255, 255);
  }
  
  .card > .img-container > img {
    height: 100%;
  }
  
  .card:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
.title {
    width: 100%;
    height: 3rem;
    text-align: center;
    font-size: 1.5rem;
    font-weight: bolder;
}
* {
  box-sizing: border-box;
}

html,
body,
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
}

